.footer {
  @include flex-relative;
  overflow: hidden;
  height: 450px;
  background-color: $dark-color;

  &__decoration {
    position: relative;
    top: -200px;
    left: -20%;
    width: 650px;
    height: 600px
  }

  &__logo {
    height: 30px;
    margin-right: 100px;
  }

  &__wrapper {
    @include flex-absolute;
    margin: 50px 0 0 50px;
  }

  &__group-community,
  &__group-contacts {
    margin: 0 50px;
    width: 220px
  }

  &__item {
    color: $light-color;
    font-weight: $font-light;
    font-size: 16px;
    line-height: 38px;

    &:hover {
      color: $accent-color;
      cursor: pointer;
    }
  }

  &__link {
    color: $light-color;
    text-decoration: none;

    &:hover {
      color: $accent-color;
    }
  }

  &__link-icon {
    padding: 10px 20px;
    
    &:hover {
      @include hover-pictures
    }
  }

  &__networks{
    display: flex;
  }

  &__payment {
    @include flex-relative;
    top: 300px;
    left: 20%;
  }

  &__icon-payment {
    padding: 10px;

    &:hover {
      @include hover-pictures
    }
  }

  &__line {
    position: absolute;
    height: 2px;
    width: 90%;
    top: 80%;
    left: 5%;
    background-color: $light-color;
    opacity: 0.4;
  }

  &__postfooter {
    @include flex-spaced;
    position: absolute;
    padding: 0 60px;
    top: 400px;
    width: 100%;
    color: $light-color;
    font-weight: $font-light;
    font-size: 12px;
    line-height: 18px;
    opacity: 0.4;
  }


  @media (max-width: $mobile-screen-breakpoint) {
    &__logo {
      height: 20px;
    }

    &__wrapper {
      flex-direction: column;
    }

    &__group-community,
    &__group-contacts {
      margin: 20px 0;
    }

    &__networks {
      position: absolute;
      top: -10px;
      left: 200px;
    }

    &__link-icon {
      padding: 10px;
    }

    &__payment {
      top: 360px;
      left: -610px;
    }

    &__postfooter {
      padding-top: 10px;
    }
  }
}
